#news-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #e9e9e9;
    padding: 26px 30px;
    padding-bottom: 70px;
}

#news-page h1 {
    font-family: MotoGP;
    margin: 0;
    font-size: 40px;
    padding-bottom: 30px;
    border-bottom: 1.5px solid black;
    width: 400px;
    text-align: center;
}

#news-page .card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 50px 70px;
    margin-top: 50px;
}

p {
    margin-block-start: 0px;
    margin-block-end: 0px;
}

@media only screen and (max-width: 465px) {
    #news-page h1 {
        font-size: 35px;
        width: 300px;
    }
}